import Vue from 'vue'
import Router from 'vue-router'
import index from "./components/tabbar/index.vue"
import login from "./components/tabbar/login.vue"
import shopCar from "./components/tabbar/shopCar.vue"
import shopList from "./components/tabbar/shopList.vue"
import message from "./components/tabbar/message.vue"
import details from "./components/tabbar/details.vue"
import address from "./components/tabbar/address.vue"

Vue.use(Router)
export default new Router({
  routes: [
    {path:'/',component:index},
    {path:'/index',component:index},
    {path:'/login',component:login},
    {path:'/shopCar',component:shopCar},
    {path:'/shopList',component:shopList},
    {path:'/message',component:message},
    {path:'/details/:lid',component:details},
    {path:'/address',component:address},
  ]
})
