<template>
    <div class="app_index">
        <!--头部-->
        <my-header></my-header>
        <!--gotop-->
        <a id="gotop" href="#"></a>
        <!--搜索-->
        <div class="search">
            <div>
                <input id="ind_search" type="text" placeholder="搜索菜谱、食材或菜单...">
                <a id="ind_search_btn" href="javascript:;">
                    <span>搜索</span>
                </a>
            </div>
        </div>
        <!--轮播-->
        <mt-swipe :auto="4000">
            <mt-swipe-item v-for="(item,i) of list" :key="i">
                <img :src="item.img_url">
            </mt-swipe-item>
        </mt-swipe>
        <!--九宫格-->
        <ul class="mui-table-view mui-grid-view mui-grid-9">
            <li class="mui-table-view-cell mui-media mui-col-xs-3 mui-col-sm-3" v-for="(item,i) of lista" :key="i">
                <router-link :to="item.href">
                    <img :src="item.img_url">
                    <div class="mui-media-body">{{item.tit}}</div>
                </router-link>
            </li>
        </ul> 
        <!--唯一-->
        <div class="num1">
            <div>
                <a href="javascript:;">
                    <p>蜂蜜西柚</p>
                    <img src="img/index/fmxy.gif" alt="">
                    <span>菜谱</span>
                </a>
            </div>
        </div>
        <!--精选-->
        <div class="nice">
            <ul class="n1">
                <li v-for="(item,i) of listb" :key="i">
                    <router-link :to="item.href">
                        <h2>{{item.tit}}</h2>
                        <div><img :src="item.img_a" alt=""></div>
                        <div><img :src="item.img_b" alt=""></div>
                        <div><img :src="item.img_c" alt=""></div>
                        <div><img :src="item.img_d" alt=""></div>
                        <span>{{item.span}}</span>
                    </router-link>
                </li>
            </ul>
        </div>
        <!--推荐-->
        <div class="recommend">
            <ul class="rec">
                <li v-for="(item,i) of listc" :key="i">
                    <router-link :to="item.href">
                        <img :src="item.img_url" alt="">
                        <p>{{item.tit}}</p><br>
                        <span>{{item.span_a}}</span><br>
                        <span>{{item.span_b}}</span>
                    </router-link>
                </li>
            </ul>
        </div>
        <mt-button size="large" type="primary" @click="loadMore">加载更多</mt-button>
    </div>
</template>
<script>
import myHeader from '@/components/header.vue'
export default {
    data(){
        return {
            list:[],
            lista:[],
            listb:[],
            listc:[],
            pno:1,
            hasMore:true,
        }
    },
    created(){
        this.loadImgList();
        this.loadImgGrid();
        this.loadImgjx();
        this.loadMore();
    },
    methods:{
        loadImgList(){
            var url="imglist";
            this.axios.get(url).then(result=>{
                this.list=result.data;
            })
        },
        loadImgGrid(){
            var url="imggrid";
            this.axios.get(url).then(result=>{
                this.lista=result.data;
            })
        },
        loadImgjx(){
            var url="jxImg";
            this.axios.get(url).then(result=>{
                this.listb=result.data;
            })
        },
        loadMore(){
            if(this.hasMore==false){
                this.$toast("加载已完成");
                return;
            }
            var n=parseInt(this.pno++);
            var url="recommend?pno=";
            url+=n;
            this.axios.get(url).then(result=>{
                //获取服务器数据
                //将返回数据保存listc
                this.listc=this.listc.concat(result.data.data);
                if(this.pno>=result.data.pageCount){
                    this.hasMore=false;
                }
            })
        }
    },
    components:{
        myHeader,
    }
}
</script>
<style scoped>
    *{margin: 0;padding: 0;}
    /*gotop*/
    #gotop{
        background: url("/img/index/gotop.png") no-repeat;
        display: inline-block;
        width: 40px;
        height: 40px;
        background-size: 100%;
        position: fixed;
        left: 50%;
        margin-left: -20px;
        bottom: 5%;
        z-index: 100;
    }
    /*搜索*/
    .search{
        width: 100%;
        height: 66px;
        padding: 10px 5px;
        z-index: 101;
    }
    .search>div{
        height: 44px;
        line-height: 44px;
        border: 1px solid #bbb;
        border-radius: 3px;
        position: relative;
        overflow: hidden;
    }
    .search #ind_search{
        float: left;
        border: 0;
        font-size: 15px;
        width: 100%;
        height: 44px;
        text-indent: 10px;
    }
    .search #ind_search_btn{
        display: block;
        float: right;
        width: 70px;
        position: absolute;
        top: 0;
        right: 0;
        height: 100%;
        color: #333;
    }
    .search #ind_search_btn>span{
        display: block;
        line-height: 19px;
        height: 19px;
        margin: 13px 0 0;
        text-align: center;
        font-size: 15px;
        width: 100%;
        border-left: 1px solid #bbb;
    }
    /*轮播*/
    .mint-swipe{
        height:200px;
        z-index: 101;
    }
    .mint-swipe img{
        width: 100%;
        height: 200px;
    }
    /*九宫格*/
    .mui-grid-view.mui-grid-9{
        background-color: #fff;
        border-bottom: 1px solid #ddd;
        padding-top: 0;
    }
    .mui-grid-9 img{
        border-radius: 50%;
        width: 50px;
        height: 50px;
    }
    /*唯一*/
    .num1{
        width: 100%;
        height: 250px;
        z-index: 1000;
    }
    .num1>div{
        padding: 20px;
    }
    .num1>div>a>p{
        font-size: 24px;
        margin-bottom: 10px;
        color: #000;
    }
    .num1>div>a>img{
        width: 100%;
        height: 200px;
    }
    .num1>div>a>span{
        color: #888;
        font-size: 14px;
    }
    /*精选*/
    .nice{
        margin-top: 10px;
        z-index: 200;
    }
    .nice .n1{
        list-style: none;
    }
    .nice .n1 span{
        font-size: 14px;
    }
    .nice .n1 a{
        padding: 10px 15px 10px 0;
        color: #888;
    }
    .nice .n1 h2{
        width: 100%;
        font-size: 18px;
        color: #111;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        line-height: 100%;
        padding: 0 5px 10px 0;
    }
    .nice .n1 li{
        padding-left: 20px;
        padding-right: 15px;
        border-bottom: 1px solid #ddd;
    }
    .nice .n1 li div{
        width: 25%;
        float: left;
    }
    .nice .n1 li div>img{
        width: 90%;
    }
    /*推荐*/
    .recommend .rec{
        width: 100%;
        padding:10px 20px 10px 20px;
    }
    .recommend .rec li{
        width: 100%;
        height: 80px;
        list-style: none;
        margin-bottom: 10px;
    }
    .recommend .rec li a{
        display: inline-block;
        width: 100%;
        height: 80px;
        border-bottom: 1px solid #ddd;
    }
    .recommend .rec li>a>img{
        width: 80px;
        height: 80px;
        float: left;
        padding-right: 5px;
    }
    .recommend .rec li>a>p{
        font-size: 18px;
        color: #000;
        float: left;
    }
    .recommend .rec li>a>span{
        font-size: 14px;
        line-height:30px;
        width: 200px;
        height: 30px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        float: left;
        color: #888;
    }
</style>