<template>
    <div>
        <!--头部-->
        <div class="header">
            <a href="javascript:;" @click="a_go"><img src="img/index/back_icon.png" alt="美食天下"><span>返回</span></a>
            <p>购物车</p>
        </div>
        <!--购物车列表-->
        <div class="mui-card">
            <div class="mui-card-header">
                <input type="checkbox" @change="selectAll" :checked="allcb">全选
            </div>
            <div class="mui-card-content">
                <div class="mui-card-content-inner">
                    <ul class="mui-table-view">
                        <li class="mui-table-view-cell mui-media" v-for="(item,i) of list" :key="i">
                            <a class="food" href="javascript:;">
                                <img class="mui-media-object mui-pull-left" :src="item.img_url">
                                <div class="mui-media-body">
                                    <p class='mui-ellipsis'>
                                        <!-- 1：复选框 -->
                                        <input type="checkbox" :checked="item.cb" @change="modifyItem" :data-i="i">
                                        <!-- 2：商品名称 -->
                                        <span>{{item.tit}}</span>
                                        <!-- 3：商品价格 -->
                                        <i>￥:{{item.price}}</i>
                                        <span>数量: {{item.count}}</span>
                                    </p>
                                </div>
                            </a>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="mui-card-footer">
                <input type="button" value="删除选中的商品" @click="removeSelect">
                <span>合计：{{sum}}</span>
                <input type="button" value="结算">
            </div>
        </div>
    </div>
</template>
<script>
import {MessageBox} from "mint-ui"
export default {
    data(){
        return {
            list:[],
            allcb:false,
        }
    },
    created() {
        this.loadMore();
    },
    methods:{
        a_go(){
            this.$router.go(-1);
        },
        /*向服务器发送请求 获取购物车数据*/
        loadMore(){
            var url="getCar";
            this.axios.get(url).then(res=>{
                if(res.data.code<1){
                    this.$toast("请登录");
                    return;
                }
                //为数组元素添加属性cb cb表示复选框状态
                var rows=res.data.data;
                //创建循环遍历数组并且添加属性
                for(var item of rows){
                    item.cb=false;
                }
                //将添加结果后 数组赋值list
                this.list=rows;
            })
        },
        /*全选按钮*/
        modifyItem(e){
            var cb=e.target.checked;
            var i=e.target.dataset.i;
            this.list[i].cb=cb;
            var count=0;
            for(var item of this.list){
                if(item.cb){
                    count++;
                }
            }
            if(count==this.list.length){
                this.allcb=true;
            }else{
                this.allcb=false;
            }
        },
        selectAll(e){
            var cb=e.target.checked;
            for(var item of this.list){
                item.cb=cb;
            }
            if(this.allcb==true){
                this.allcb=false;
            }else{
                this.allcb=true;
            }
        },
        /*删除选中商品*/
        removeSelect(e){
            this.$messagebox.confirm("确定删除选中的商品吗？")
            .then(action=>{
                var ids="";
                for(var item of this.list){
                    if(item.cb){
                        ids+=item.id+",";
                    }
                }
                if(ids.length==0){
                    this.$toast("请选中您需要删除的商品");
                    return;
                }
                ids=ids.substring(0,ids.length-1);
                var url="delM?ids="+ids;
                this.axios.get(url).then(result=>{
                    if(result.data.code==1){
                        this.$toast(result.data.msg);
                        this.loadMore();
                    }
                })
            })
            .catch(result=>{
                this.$toast("已取消");
            })
        }
    },
    computed:{
        sum(){
            var sum=0;
            for(var i=0;i<this.list.length;i++){
                var item=this.list[i];
                if(item.cb){
                    sum+=item.price*item.count;
                }
            }
            return sum;
        }
    },
}
</script>
<style scoped>
    /*头部*/
    .header{
        width: 100%;
        height: 50px;
        background: #fff;
        position: relative;
    }
    .header>a:first-child span{
        color: #ff5151;
        position: relative;
        top: -18px;
        left: -5px;
    }
    .header>a:first-child img{
        width: 20px;
        height: 50px;
        margin-left: 15px;
    }
    .header>p{
        width: 180px;
        height: 50px;
        line-height: 50px;
        text-align: center;
        color: #000;
        font-size: 18px;
        position: relative;
        top: -52px;
        left: 50%;
        margin-left: -90px;
    }
    /*购物车列表*/
    .food>img{
        width: 60px;
        height: 42px;
    }
    .mui-media-body{
        line-height: 40px;
    }
    .mui-media-body .count{
        display: inline-block;
        width: 20px;
        height: 20px;
        text-align: center;
        line-height: 20px;
    }
    .food .mui-media-body .mui-ellipsis>span{
        display: inline-block;
        width: 40%;
    }
    .food .mui-media-body .mui-ellipsis>i{
        font-style: normal;
        display: inline-block;
        width: 30%;
    }
</style>