<template>
    <div>
        <!--头部-->
        <div class="header">
            <a href="javascript:;" @click="a_go"><img src="img/index/back_icon.png" alt="美食天下"><span>返回</span></a>
            <p>{{list.tit}}</p>
        </div>
        <!--内容-->
        <div class="dets">
            <img :src="list.img_url">
            <p>半年销量:<i>{{list.i_a}}</i></p>
            <div class="js">
                <i>介绍:</i>
                <span>{{list.span_a}}</span>
                <i>主料</i>
                <span>{{list.span_b}}</span>
                <i>辅料</i>
                <span>{{list.span_c}}</span>
                <div class="mui-numbox" style="margin-top:10px">
                    <button class="mui-btn mui-btn-numbox-minus" type="button" @click="goodSub">-</button>
                    <input class="mui-input-numbox" type="number" value="1" v-model="val">
                    <button class="mui-btn mui-btn-numbox-plus" type="button" @click="goodAdd">+</button>
                </div>
                <strong>单价:{{list.price}}</strong>
            </div>
        </div>
        <!--加入购物车-->
        <div class="add">
            <div class="dp">
                <i></i>
                <span>店铺</span>
            </div>
            <div class="kf">
                <i></i>
                <span>客服</span>
            </div>
            <div class="shopCar" @click="div_btn">
                <i></i>
                <span>购物车</span>
            </div>
            <div class="car" @click="addCar">加入购物车</div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            list:[],
            id:this.$route.params.lid,
            val:1,
        }
    },
    created(){
        var id=this.$route.params.lid;
        this.details(id);
    },
    mounted(){
        // this.addCar()
    },
    methods:{
        a_go(){
            this.$router.go(-1);
        },
        details(id){
            var url="details";
            this.axios.get(url).then(res=>{
                if(res.data.code==1){
                this.list=res.data.data[id-1];
                };
            })
        },
        div_btn(){
            this.$router.push("/shopCar");
        },
        //数量加减
        goodAdd(){
            this.val++;
        },
        goodSub(){
            if(this.val>1){
                this.val--;
            }
        },
        //点击加入购物车
        addCar(){
            var tit=this.list.tit;
            var img_url=this.list.img_url;
            var pid=this.list.pid;
            var count=this.val;
            var price=this.list.price;
            //判断用户是否登录 如果未登录 请先登录
            var url="getInfo";
            this.axios.get(url).then(result=>{
                if(!result.data){
                    this.$toast("请先登录，3秒后跳转登录页");
                    setTimeout(go=>{
                        this.$router.push("/login");
                        return;
                    },3000);
                }else{
                    //发送ajax请求
                    var url="addCar?tit="+tit+"&img_url="+img_url+"&pid="+pid+"&count="+count+"&price="+price;
                    this.axios.get(url).then(result=>{
                        if(result.data.code==1){
                            this.$toast("添加成功");
                        }else{
                            this.$toast("添加失败");
                        }
                    })       
                }
            })
        }
    },
}
</script>
<style scoped>
    /*头部*/
    .header{
        width: 100%;
        height: 50px;
        background: #fff;
        position: relative;
    }
    .header>a:first-child span{
        color: #ff5151;
        position: relative;
        top: -18px;
        left: -5px;
    }
    .header>a:first-child img{
        width: 20px;
        height: 50px;
        margin-left: 15px;
    }
    .header>p{
        width: 180px;
        height: 50px;
        line-height: 50px;
        text-align: center;
        color: #000;
        font-size: 18px;
        position: relative;
        top: -52px;
        left: 50%;
        margin-left: -90px;
    }
    /*内容*/
    .dets{
        width: 100%;
        height: 100%;
    }
    .dets>img{
        padding: 10px 20px;
        width: 100%;
        height: 250px;
    }
    .dets .js{
        width: 100%;
        height: 100%;
        padding:0px  20px;
    }
    .dets .js>span{
        display: block;
        width: 100%;
        height: 100%;
        text-indent: 2em;
    }
    .dets .js>i{
        font-style: normal;
        font-size: 20px;
        color: #000;
        font-weight: 900;
        display: block;
    }
    .dets .js>strong{
        font-weight: normal;
        font-style: normal;
        margin-left: 20px;
        font-size:18px;
        color: #e93b3d;
        position: relative;
        top: 6px;
    }
    .dets p{
        margin-left: 20px;
        font-weight: normal;
        font-style: normal;
        color: rgba(0,0,0,0.5);
    }
    /*加入购物车*/
    .add{
        width: 100%;
        height: 60px;
        background: #fff;
        position: fixed;
        bottom:0px;
    }
    .add .dp{
        float: left;
        width: 15%;
        height: 60px;
        text-align: center;
        line-height: 10px;
    }
    .add .dp>i{
        background: url("/img/details/dp.png") no-repeat;
        background-size: 100%;
        display: block;
        width: 20px;
        height: 30px;
        margin-top: 10px;
        margin-left: 20px;
    }
    .add .kf{
        float: left;
        width: 15%;
        height: 60px;
        text-align: center;
        line-height: 10px;
    }
    .add .kf>i{
        background: url("/img/details/kf.png") no-repeat;
        background-size: 100%;
        display: block;
        width: 20px;
        height: 30px;
        margin-top: 10px;
        margin-left: 20px;
    }
    .add .shopCar{
        float: left;
        width: 20%;
        height: 60px;
        line-height: 10px;
        text-align: center;
    }
    .add .shopCar>i{
        background:url("/img/details/shopCar.png") no-repeat;
        background-size: 100%;
        display: block;
        width: 20px;
        height: 30px;
        margin-top: 10px;
        margin-left: 20px;
    }
    .add .car{
        display: inline-block;
        width: 50%;
        height: 60px;
        line-height: 60px;
        color: #fff;
        text-align: center;
        font-size: 28px;
        background: linear-gradient(-41deg,#ff4f18,#ff2000 24%,#f10000);
    }
</style>