<template>
    <div id="app">
        <!--头部-->
        <div class="header">
            <a href="javascript:;" @click="a_go"><img src="img/index/back_icon.png" alt="美食天下"><span>返回</span></a>
            <p>定位</p>
        </div>
        <!--定位-->
        <div id="allmap" ref="allmap"></div>
        <router-view></router-view>
    </div>
</template>
 <script>
 export default {
    data(){
        return {
            name: 'App',
        }
    },
    methods:{
        a_go(){
            this.$router.go(-1);
        },
        map(){
        let map =new BMap.Map(this.$refs.allmap); // 创建Map实例
        map.centerAndZoom(new BMap.Point(113.664392,34.82581), 18);// 初始化地图,设置中心点坐标和地图级别
        map.addControl(new BMap.MapTypeControl({//添加地图类型控件
        mapTypes:[
        BMAP_NORMAL_MAP,
        BMAP_HYBRID_MAP
        ]}));
        map.setCurrentCity("郑州");// 设置地图显示的城市 此项是必须设置的
        map.enableScrollWheelZoom(true); //开启鼠标滚轮缩放
        }
        },
    mounted(){
        this.map()
        }
    }
 </script>
 <style scoped>
    /*头部*/
    #app .header{
        width: 100%;
        height: 50px;
        background: #fff;
        position: relative;
    }
    #app .header>a:first-child span{
        color: #ff5151;
        position: relative;
        top: -18px;
        left: -150px;
    }
    #app .header>a:first-child img{
        width: 20px;
        height: 50px;
        position:relative;
        left: -150px;
    }
    #app .header>p{
        width: 180px;
        height: 50px;
        line-height: 50px;
        text-align: center;
        color: #000;
        font-size: 18px;
        position: relative;
        top: -52px;
        left: 50%;
        margin-left: -90px;
    }
    /*定位*/
    #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    }
    #allmap{
    height: 625px;
    overflow: hidden;
    }
 </style>