<template>
    <div class="app_message">
        <!--头部-->
        <div class="header">
            <a href="javascript:;" @click="a_go"><img src="img/index/back_icon.png" alt="美食天下"><span>返回</span></a>
            <p>留言板</p>
        </div>
        <!--留言板-->
        <!--输入部分-->
        <div class="sr">
            <textarea name="" id="" cols="30" rows="10" v-model="val"></textarea>
            <input type="submit" value="发布" @click="report">
        </div>
        <!--显示部分-->
        <div class="xs">
            <div v-for="(item,i) of list" :key="i">
                <p>{{item.uname}}</p>
                <p>{{item.sj}}</p><br>
                <span>{{item.txt}}</span>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            val:"",
            list:[],
        }
    },
    methods:{
        a_go(){
            this.$router.go(-1);
        },
        report(){
            var myDate=new Date();
            var vv=myDate.toLocaleString();
            var val=this.val;
            var url="getInfo";
            if(!val.trim()){
                this.$toast("输入内容不能为空,请重新输入");
                return;
            }
            this.axios.get(url).then(result=>{
                if(!result.data){
                    this.$toast("请先登录，3秒后跳转登录页");
                    setTimeout(go=>{
                        this.$router.push("/login");
                        return;
                    },3000);
                }else{
                    //发送ajax请求
                    var url ="report?text="+val+"&sj="+vv;
                    this.axios.get(url).then(result=>{
                        this.list=result.data;
                        this.val="";
                    })
                }
            })
        }
    }
}
</script>
<style scoped>
    /*头部*/
    .header{
        width: 100%;
        height: 50px;
        background: #fff;
        position: relative;
    }
    .header>a:first-child span{
        color: #ff5151;
        position: relative;
        top: -18px;
        left: -5px;
    }
    .header>a:first-child img{
        width: 20px;
        height: 50px;
        margin-left: 15px;
    }
    .header>p{
        width: 180px;
        height: 50px;
        line-height: 50px;
        text-align: center;
        color: #000;
        font-size: 18px;
        position: relative;
        top: -52px;
        left: 50%;
        margin-left: -90px;
    }
    /*输入内容*/
    .sr{
        padding-left: 10px;
    }
    .sr>textarea:first-child{
        margin-top: 10px;
        width: 90%;
        height: 150px;
        display: block;
        word-break: break-all;
        width: 90%;
    }
    .sr>input:nth-child(2){
        background: #fa7999;
        border:0;
    }
    /*显示内容*/
    .xs{
        width: 100%;
        height: 100%;
        padding: 10px 10px;
    }
    .xs>div{
        border-top: 1px solid #fff;
        background: #d5a456;
        width: 100%;
        min-height: 80px;
        padding: 10px 20px;
    }
    .xs>div>p{
        color: #fff;
        font-size: 20px;
        display: inline-block;
        float: left;
        width: 40%;
    }
    .xs>div>p:nth-child(2){
        display: inline-block;
        width: 60%;
        text-align: right;
        color: #888;
        font-size: 14px;
    }
    .xs>div>span{
        color: #333;
        font-size: 15px;
    }
</style>