<template>
    <div class="app_login">
        <!--头部-->
        <div class="header">
            <router-link to="/"><img src="img/index/back_icon.png" alt="美食天下"><span>返回</span></router-link>
            <p>登录美食天下</p>
        </div>
        <!--选项卡-->
        <div class="mui-content">
			<div style="padding: 10px 10px;">
				<div id="segmentedControl" class="mui-segmented-control">
					<a class="mui-control-item mui-active" href="#item1">登录</a>
					<a class="mui-control-item" href="#item2">注册</a>
				</div>
			</div>
            <!--登录-->
            <div id="item1" class="mui-control-content mui-active">
                <input type="text" placeholder="手机号" v-model="phone">
                <input type="password" placeholder="密码" v-model="pwd">
                <button type="submit" @click="btn_login">登录</button>
            </div>
            <!--注册-->
            <div id="item2" class="mui-control-content">
                <input type="text" placeholder="手机号" v-model="phone">
                <input type="password" placeholder="密码" v-model="pwd1">
                <input type="password" placeholder="确认密码" v-model="pwd2">
                <input type="text" placeholder="用户名" v-model="uname">
                <button type="submit" @click="btn_reg">立即注册</button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return {
            phone:"",
            pwd1:"",
            pwd2:"",
            pwd:"",
            uname:"",
        }
    },
    methods:{
        btn_reg(){
            var phone=this.phone;
            var pwd1=this.pwd1;
            var pwd2=this.pwd2;
            var pwd=this.pwd;
            var uname=this.uname;
            if(pwd1.trim()===pwd2.trim()){
                pwd=pwd1;
            }else{
                this.$toast("两次密码不一致");
                return;
            }
            //判断手机号格式是否正确
            var preg=/^1[3-8]\d{9}$/;
            if(!preg.test(phone)){
                this.$toast("手机号格式不正确");
                return;
            }
            var preg=/^[0-9A-Za-z]{3,10}$/;
            if(!preg.test(pwd)){
                this.$toast("密码格式不正确");
                return;
            }
            if(!uname.trim()){
                this.$toast("用户名不能为空");
                return;
            }
            var url="reg?phone="+phone+"&pwd="+pwd+"&uname="+uname;
            this.axios.get(url).then(res=>{
                if(res.data.code==1){
                    this.$toast("注册成功");
                    this.$router.push("/index");
                }
            })
        },
        btn_login(){
            var phone=this.phone;
            var pwd=this.pwd;
            var preg=/^1[3-8]\d{9}$/;
            if(!preg.test(phone)){
                this.$toast("手机号格式不正确");
                return;
            }
            var preg=/^[0-9A-Za-z]{3,10}$/;
            if(!preg.test(pwd)){
                this.$toast("密码格式不正确");
                return;
            }
            var url="login?phone="+phone+"&pwd="+pwd;
            this.axios.get(url).then(res=>{
                if(res.data.code==-1){
                    this.$toast("手机号密码错误");
                }else{
                    this.$toast("登陆成功");
                    this.$router.push("/index");
                }
            })
        }
    }

}
</script>
<style scoped>
    /*头部*/
    .header{
        width: 100%;
        height: 50px;
        background: #fff;
        position: relative;
    }
    .header>a:first-child span{
        color: #ff5151;
        position: relative;
        top: -18px;
        left: -5px;
    }
    .header>a:first-child img{
        width: 20px;
        height: 50px;
        margin-left: 15px;
    }
    .header>p{
        width: 180px;
        height: 50px;
        line-height: 50px;
        text-align: center;
        color: #000;
        font-size: 18px;
        position: relative;
        top: -52px;
        left: 50%;
        margin-left: -90px;
    }
    /*登录模块*/
    #item1{
        text-align: center;
    }
    #item2{
        text-align: center;
    }
    #item1>input{
        margin: 0;
        border: 0;
        height: 44px;
        line-height: 24px;
        font-size: 16px;
        width: 100%;
        background: #fdfdfd;
    }
    #item2>input{
        margin: 0;
        border: 0;
        height: 44px;
        line-height: 24px;
        font-size: 16px;
        width: 100%;
        background: #fdfdfd;
    }
    .mui-content button{
        width: 250px;
        background: #007aff;
        color: #fff;
        height: 40px;
        margin-top: 10px;
    }
</style>