<template>
    <div class="app_header">
        <router-link to="/"><img src="img/index/logo.png" alt="美食天下"></router-link>
        <router-link to="/login" v-show="isshow">登录</router-link>
        <div class="welcome" v-show="!isshow">
            欢迎
            <span>{{uname | userFilter}}</span>
            用户
            <a href="javascript:;" @click="goout">退出</a>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return {
            uname:"",
            isshow:true,
        }
    },
    created(){
        this.getInfo();
    },
    methods:{
        getInfo(){
            var url="getInfo";
            this.axios.get(url).then(result=>{
                if(!result.data){
                    this.uname="";
                    this.isshow=true;
                }else{
                    this.isshow=false;
                    this.uname=result.data;
                }
            })
        },
        goout(){
            var url="goOut"
            this.axios.get(url).then(result=>{
                if(result.data.code=1){
                    this.$toast("退出成功");
                    this.isshow=true;
                }
            })
        }
    }
}
</script>
<style scoped>
    .app_header{
        width: 100%;
        height: 50px;
        background: #fff;
        position: relative;
    }
    .app_header>a:first-child img{
        width: 120px;
        height: 30px;
        margin-top: 10px;
        margin-left: 15px;
    }
    .app_header>a:nth-child(2){
        position: absolute;
        top: 20px;
        right: 15px;
        color: #ff5151;
    }
    .welcome{
        position: absolute;
        top: 20px;
        right: 20px;
        font-size: 14px;
    }
    .welcome span{
        color: #ff5151;
    }
</style>